// Dependencies
import classnames from 'classnames';
import React, { useState } from 'react';

// Components
import Modal from '../../../components/Modal';

// Utils
import isMobile from 'Utils/isMobile';

const MODAL_WELCOME_DESKTOP_FOLDER_PUBLIC_URL =
  'https://storage.googleapis.com/b8one-200201.appspot.com/movize/imgs/modal-welcome/desktop';

const MODAL_WELCOME_MOBILE_FOLDER_PUBLIC_URL =
  'https://storage.googleapis.com/b8one-200201.appspot.com/movize/imgs/modal-welcome/mobile';

const ModalWelcome = ({ showModal, handleClose }) => {
  const [modalStep, setModalStep] = useState(1);

  const MODAL_WELCOME_FOLDER_PUBLIC_URL = isMobile()
    ? MODAL_WELCOME_MOBILE_FOLDER_PUBLIC_URL
    : MODAL_WELCOME_DESKTOP_FOLDER_PUBLIC_URL;

  return (
    <Modal
      handleClose={() => handleClose()}
      modalClass={classnames('modal-welcome', {
        'wrapper-modal--show': showModal,
      })}
    >
      <>
        <h2 className={'modal__title'}>Formas de navegao</h2>

        {!isMobile() && (
          <p className={'modal__paragraph'}>
            Preparamos dicas de navegao que vo te ajudar a preencher sua
            lista de mudana
          </p>
        )}

        <img
          className={'modal__img'}
          src={`${MODAL_WELCOME_FOLDER_PUBLIC_URL}/step-${modalStep}.png`}
        />

        {modalStep === 1 && (
          <p className={'modal__paragraph modal__paragraph--big'}>
            Os itens esto agrupados por ambiente e categoria. Assim voc pode
            navegar pelo catlogo selecionar os itens da sua mudana de forma
            fcil e organizada.
          </p>
        )}

        {modalStep === 2 && (
          <p className={'modal__paragraph modal__paragraph--big'}>
            Se preferir, voc pode usar a ferramenta de busca de nosso catlogo
            e pesquisar os itens de acordo com sua lista de mudana.
          </p>
        )}

        {modalStep === 3 && (
          <p className={'modal__paragraph modal__paragraph--big'}>
            Com o recurso de{' '}
            <span className={' modal__paragraph--text-orange'}>
              Salvar lista de mudana
            </span>
            , voc cria e salva diferentes listas de mudanas. Com ele, pode se
            organizar e organizar os itens de sua mudana no seu ritmo e
            convenincia.
          </p>
        )}

        <ul className={'modal__dots'}>
          {[1, 2, 3].map(step => (
            <li key={step} className={'dots__item'}>
              <button
                className={classnames('dots__button', {
                  'dots__button--active': step === modalStep,
                })}
                onClick={() => setModalStep(step)}
              ></button>
            </li>
          ))}
        </ul>

        {modalStep !== 3 && (
          <>
            <button
              className={'button button--primary'}
              onClick={() => setModalStep(modalStep + 1)}
            >
              Avanar
            </button>
            <button
              className={'button button--flat'}
              onClick={() => handleClose()}
            >
              Pular e comear
            </button>
          </>
        )}

        {modalStep === 3 && (
          <button
            className={'button button--primary'}
            onClick={() => handleClose()}
          >
            Comear
          </button>
        )}
      </>
    </Modal>
  );
};

export default ModalWelcome;
